.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #8a2b06;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 3%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.main-image {
  width: 100%;
  height: 25rem;
  z-index: 0;
  overflow: hidden;
}

.main-image img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}

.rightHeader {
  display: flex;
  align-items: center;
  flex-direction: row;
  gap: 2rem;
}
.adminContainer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.adminHolder{
  display: flex;
  flex-direction: row;
  overflow: none;
  justify-content:center;
  align-items: center;
}
.admin a {
  color: white;
}

.adminContainer h4 {
  display: none;
}
@media (max-width: 500px) {
  .logo {
    display: none;
  }
  .header {
    justify-content: center;
  }
  .adminContainer h4 {
    display: inline-block;
  }
}
